import React, {useContext} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

import { CurrentEquipContext } from '../../context/currentEquip-context';
import {useCardsStyles} from './CommonCard'
import CardRow from './CardRow'

const SystemCard = React.memo((props) => {
//export default function SystemCard() {
  console.log(`! render SystemCard`);

  const classes = useCardsStyles();
  const bull = <span className={classes.bullet}></span>;

  const system = props.system;
  return (
    <Card className={classes.root}>
      <CardContent>
        <Typography variant="h5" component="h2">
          {bull}CPU
        </Typography>
        <CardRow descr="" value={system?.Processor?.Model} rightWidth={'100%'}></CardRow>
        <CardRow descr="" value={system?.Processor?.CPU_Load+'%'}></CardRow>

        <Typography variant="h5" component="h2">
          {bull}
        </Typography>
        <CardRow descr="" value={system?.Memory?.Memory_total_Gb+' '}></CardRow>
        <CardRow descr="" value={system?.Memory?.Memory_free_Gb+' '}></CardRow>

        <Typography variant="h5" component="h2">
          {bull}
        </Typography>
        <Typography variant="h6" component="h2">
              {bull} 
        </Typography>
        {system?.HDD?.map((i, ind) => (
          <div key={ind.toString()} >
            <Typography variant="h6" component="h2">
              {bull} {i.Letter}
            </Typography>
            <CardRow descr="" value={i.TotalSize+' '}></CardRow>
            <CardRow descr="" value={i.FreeSize+' '}></CardRow>
          </div>
          ))
        } 
        <Typography variant="h6" component="h2">
              {bull} 
        </Typography>
        {system?.PhysicalDisks?.map((i, ind) => (
          <div key={ind.toString()} >
            <Typography variant="h6" component="h2">
              {bull} {i.MediaType}
            </Typography>
            <CardRow descr={i.FriendlyName+' '} value={i.Size_Gb+' '}></CardRow>
          </div>
          ))
        } 

        <Typography variant="h5" component="h2">
          {bull}
        </Typography>
        {system?.Network?.filter(i => i.NIC).map((i, ind) => (
          <CardRow key={ind.toString()} descr={i.NIC} value={i.IP}></CardRow>
          ))
        } 

        <Typography variant="h5" component="h2">
          {bull}
        </Typography>
        {system?.VGA?.map((i, ind) => (
          <div key={ind.toString()} >
            <CardRow descr="" value={i.Card_Name} rightWidth={'100%'}></CardRow>
            <CardRow descr="" value={i.Memory_Gb+' '}></CardRow>
            <CardRow descr="" value={i.Driver_Version}></CardRow>
          </div>
          ))
        } 

        <Typography variant="h5" component="h2">
          {bull}
        </Typography>
        {system?.Monitor?.map((i, ind) => (
          <CardRow key={ind.toString()} descr={i.Device_Name} value={i.Width+'x'+i.Height}></CardRow>
          ))
        } 

        <Typography variant="h5" component="h2">
          {bull}
        </Typography>
        {system?.Printer?.map((i, ind) => (
          <div key={ind.toString()} >
            <CardRow descr="" value={i.Printer_Name} rightWidth={'100%'}></CardRow>
            <CardRow descr="" value={i.Port_Name} rightWidth={'100%'}></CardRow>
          </div>
          ))
        } 
      </CardContent>
    </Card>
  );
});

export default SystemCard;