import React from 'react';
import CommonTable from './CommonTable'

const columns = [
    { id: 'EquipName', label: '', minWidth: 170 },
    { id: 'DateTime', label: '', minWidth: 100 },
    { id: 'State', label: '', minWidth: 100 },
    { id: 'CPU_Load', label: 'CPU ', minWidth: 100 },
    { id: 'TotalMemory', label: ' ', minWidth: 100 },
    { id: 'AvailableSize', label: ' ', minWidth: 100 },
    { id: 'HddName', label: ' ', minWidth: 100 },
    { id: 'HddTotalSpace', label: '', minWidth: 100 },  
    { id: 'HddFreeSpace', label: '', minWidth: 100 },  
  ];
  
export default function SystemTable(props) {
  console.log("render SystemTable");

  const rows = props.data;
  return (
    <CommonTable columns={columns} rows={rows}></CommonTable>
  );
}